main {
  position: relative;
  min-height: 100vh;
}

.cards {
  margin: 5rem 1.5rem;
}
@media (min-width: 40em) {
  .cards {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 11%;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: 1fr 3fr;
    gap: 1.875rem;
  }
}
.cards__user {
  background-color: #1c1f4a;
  border-radius: 0.9375rem;
}
.cards__user__info {
  border-radius: inherit;
}

.cards__user {
  background-color: #1c1f4a;
  border-radius: 0.9375rem;
}
.cards__user__info {
  border-radius: inherit;
  background-color: #5847eb;
  padding: 1.875rem;
  display: flex;
  flex-direction: row;
}
@media (min-width: 40em) {
  .cards__user__info {
    padding: 2.1875rem 1.875rem;
    flex-direction: column;
  }
}
@media (max-width: 39.9375em) {
  .cards__user__info {
    align-items: center;
  }
}
.cards__user__info .image {
  border: 0.1875rem solid white;
  border-radius: 50%;
  height: 4.375rem;
  width: 4.375rem;
}
@media (min-width: 40em) {
  .cards__user__info .image {
    display: block;
    height: 5.3125rem;
    width: 5.3125rem;
  }
}
.cards__user__info .image img {
  width: 100%;
}
.cards__user__info .name {
  margin: 0 0 0 1.25rem;
}
@media (min-width: 40em) {
  .cards__user__info .name {
    margin: 2.5rem 0;
  }
}
.cards__user__info .name__report {
  font-size: 0.875rem;
  font-weight: 400;
  color: #bdc1ff;
}
@media (min-width: 40em) {
  .cards__user__info .name__report {
    font-size: 1rem;
  }
}
.cards__user__info .name__name {
  font-size: 1.5rem;
  font-weight: 300;
  color: white;
}
@media (min-width: 40em) {
  .cards__user__info .name__name {
    font-size: 2.375rem;
  }
}
.cards__user__menu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 1.5625rem;
}
@media (min-width: 40em) {
  .cards__user__menu {
    flex-direction: column;
    gap: 1.25rem;
  }
}
.cards__user__menu .tab {
  font-family: "Rubik", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  text-decoration: none;
  color: #6f76c8;
}
.cards__user__menu .tab:hover {
  color: white;
}
.cards__user__menu .active {
  color: white;
}

.cards__data {
  display: grid;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .cards__data {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1.875rem;
  }
}

.card {
  border-radius: 0.9375rem;
  background-repeat: no-repeat;
  background-position-x: 90%;
  padding-top: 2.8125rem;
}
.card__main {
  padding: 1.875rem;
  border-radius: inherit;
  background-color: #1c1f4a;
  height: 100%;
}
.card__main__menu {
  color: white;
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: 0.625rem;
}
.card__main__menu::after {
  content: url(/images/icon-ellipsis.svg);
  float: right;
}
.card__main__data {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 40em) {
  .card__main__data {
    flex-direction: column;
    justify-content: left;
  }
}
@media (max-width: 39.9375em) {
  .card__main__data {
    align-items: center;
  }
}
.card__main__data__hours {
  color: white;
  font-size: 2.25rem;
  font-weight: 300;
}
@media (min-width: 40em) {
  .card__main__data__hours {
    font-size: 3.5rem;
    font-weight: 300;
    margin-bottom: 1.25rem;
  }
}
.card__main__data__last {
  color: #bdc1ff;
  font-size: 1rem;
  font-weight: 400;
}
.card__main:hover {
  filter: brightness(175%);
  cursor: pointer;
}
.card--work {
  background-image: url(/images/icon-work.svg);
  background-color: #ff8c66;
}
.card--play {
  background-image: url(/images/icon-play.svg);
  background-color: #56c2e6;
}
.card--study {
  background-image: url(/images/icon-study.svg);
  background-color: #ff5c7c;
}
.card--exercise {
  background-image: url(/images/icon-exercise.svg);
  background-color: #4acf81;
}
.card--social {
  background-image: url(/images/icon-social.svg);
  background-color: #7536d3;
}
.card--self {
  background-image: url(/images/icon-self-care.svg);
  background-color: #f1c65b;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Rubik", sans-serif;
}

body {
  background-color: #0f1424;
}